@import url(https://fonts.googleapis.com/css2?family=Lato:wght@900&display=swap);
body {
  background-color: rgb(232,244,247);
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.nav-grid {
    display: grid;
    grid-gap: 50px;
    gap: 50px;
    grid-template-columns: repeat(auto-fit, minmax(400px, 1fr));
    grid-template-rows: auto;
    padding: 120px 50px;
}

.nav-item {
    text-decoration: none;
}

.nav-header {
    font-family: 'Lato', sans-serif;
    font-size: 40px;
    font-weight: 900;
    padding: 20px 0 0 0;
    text-align: center;
}

.nav-img {
    width: 100px;
    height: 100px;
}
.row-grid {
    display: grid;
    grid-template-columns: minmax(100px,1fr) minmax(100px,3fr);
    align-items: center;
}

.stat-grid {
    display: grid;
    grid-template-columns: minmax(100px,1fr) minmax(50px,1fr) minmax(100px,4fr);
}

.row-grid > div,
.stat-grid > div {
    padding: 15px 20px;
}

.stat-value {
    justify-self: end;
}
.type-flex {
    display: flex;
    grid-gap: 10px;
    gap: 10px;
    flex-wrap: wrap;
}

.topbar-grid {
    display: grid;
    grid-template-columns: minmax(250px,1fr) minmax(200px, 3fr);
    align-items: center;
}

.topbar-grid > div {
    padding: 15px;
}

.topbar-search {
    display: flex;
}

.topbar-back {
    justify-self: center;
}

.topbar-title {
    display: flex;
    justify-content: center;
    align-items: center;
}
.move-grid {
    display: grid;
    grid-gap: 30px;
    gap: 30px;
    grid-template-columns: repeat(auto-fit, 400px);
    grid-template-rows: auto;
    margin: 50px;
    margin-top: 120px;
    justify-items: center;
    place-content: center;
}
.sprite-image {
    width: 200px;
    height: 200px;
}

.art-image {
    width: 350px;
    height: 350px;
}

.flex-center {
    display: flex;
    justify-content: center;
}

.dex-grid {
    display: grid;
    grid-gap: 20px;
    gap: 20px;
    grid-template-columns: repeat(auto-fit, minmax(450px, 1fr));
}

.dex-grid > div {
    padding: 20px 50px;
}
.chart-header {
    padding-left: 15px;
}
.poke-link {
    text-decoration: none;
    color: black;
}

.pokecard-grid {
    display: grid;
    grid-gap: 30px;
    gap: 30px;
    grid-template-columns: repeat(auto-fit, 250px);
    padding: 120px 20px;
    place-content: center;
}

.pokecard-title {
    text-align: center;
    padding: 10px;
    padding-top: 0;
}

.poke-img {
    max-width: 250px;
    padding: 20px;
}
.blank {
    align-items: center;
    justify-content: center;
    display: flex;
    font-size: 25px;
    font-weight: 900;
    height: 100vh;
    width: 100vw;
}
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

